//mixin
@mixin list-reset {
  margin: 0;
  padding: 0;
  list-style-type: none;
}

@mixin a-reset {
  text-decoration: none;
}

@mixin base-font-style {
  color: $color-gray-6;
  font-family: $font-body;
  font-size: 14px;
  font-style: normal;
  font-weight: 400;
  line-height: 18px;
}

@mixin head-font-style {
  color: $basic-black;
  text-align: center;
  font-family: $font-header;
  font-size: 36px;
  font-style: normal;
  font-weight: 400;
  line-height: 40px;
}

@mixin title-catalog {
  color: $color-gray-6;
  font-family: $font-header;
  font-size: 16px;
  font-style: normal;
  font-weight: 400;
  line-height: 20px;
  text-transform: uppercase;
}
